<template>
  <div class="review">
      <div class="stepper">
            <div class="stepper-item">
                <div class="stepper-icon" v-bind:class="{ active: formData.isCompleted }">
                    <svg xmlns="http://www.w3.org/2000/svg" fill="none" viewBox="0 0 24 24" stroke="currentColor">
                        <path stroke-linecap="round" stroke-linejoin="round" stroke-width="2" d="M9 12l2 2 4-4m6 2a9 9 0 11-18 0 9 9 0 0118 0z" />
                    </svg>
                    <p class="stepper-number">1</p>
                </div>
              My review
            </div>
            <div class="stepper-item">
                <div class="stepper-icon" v-bind:class="{ active: preview.isCompleted }">
                    <svg xmlns="http://www.w3.org/2000/svg" fill="none" viewBox="0 0 24 24" stroke="currentColor">
                        <path stroke-linecap="round" stroke-linejoin="round" stroke-width="2" d="M9 12l2 2 4-4m6 2a9 9 0 11-18 0 9 9 0 0118 0z" />
                    </svg>
                    <p class="stepper-number">2</p>
                </div>
                Preview Review
            </div>
            <div class="stepper-item">
                <div class="stepper-icon">
                    <svg xmlns="http://www.w3.org/2000/svg" fill="none" viewBox="0 0 24 24" stroke="currentColor">
                        <path stroke-linecap="round" stroke-linejoin="round" stroke-width="2" d="M9 12l2 2 4-4m6 2a9 9 0 11-18 0 9 9 0 0118 0z" />
                    </svg>
                    <p class="stepper-number">3</p>
                </div>
                Mint Review
            </div>
      </div>

        <div v-show="activeTab==1" class="tab tab1">
            <form id="review-form">
                <div class="form-item" v-bind:class="{ error: formData.walletAddress.hasError }">
                    <label for="recipient-address">Recipient wallet address</label>
                    <div class="input-wrapper">
                        <input type="text" id="recipient-address" name="recipient-address" v-model.trim="formData.walletAddress.value">
                        <!-- <svg @click="pasteAddress" class="paste-icon" xmlns="http://www.w3.org/2000/svg" fill="none" viewBox="0 0 24 24" stroke="currentColor">
                            <path stroke-linecap="round" stroke-linejoin="round" stroke-width="2" d="M9 5H7a2 2 0 00-2 2v12a2 2 0 002 2h10a2 2 0 002-2V7a2 2 0 00-2-2h-2M9 5a2 2 0 002 2h2a2 2 0 002-2M9 5a2 2 0 012-2h2a2 2 0 012 2" />
                        </svg> -->
                    </div>
                    <div class="error-field">
                        This field can't be empty!
                    </div>
                </div>

                <div class="form-item" v-bind:class="{ error: formData.rating.hasError }">
                    <label for="rating">Rating</label>
                    <star-rating 
                    v-model:rating="formData.rating.value"
                    v-bind:increment="0.5"
                    ></star-rating>
                    <div class="error-field">
                        This field can't be empty!
                    </div>
                </div>

                <div class="form-item" v-bind:class="{ error: formData.review.hasError }">
                    <label for="review-text">Write a review</label>
                    <textarea id="review-text" name="review-text" v-model.trim="formData.review.value" rows="4"></textarea>
                    <div class="error-field">
                        This field can't be empty!
                    </div>
                </div>

                <div class="form-item" v-bind:class="{ error: formData.referenceLink.hasError }">
                    <label for="recipient-link">Link to reference</label>
                    <div class="input-wrapper">
                        <input type="text" id="recipient-link" name="recipient-link" v-model.trim="formData.referenceLink.value">
                        <!-- <svg class="paste-icon" xmlns="http://www.w3.org/2000/svg" fill="none" viewBox="0 0 24 24" stroke="currentColor">
                            <path stroke-linecap="round" stroke-linejoin="round" stroke-width="2" d="M9 5H7a2 2 0 00-2 2v12a2 2 0 002 2h10a2 2 0 002-2V7a2 2 0 00-2-2h-2M9 5a2 2 0 002 2h2a2 2 0 002-2M9 5a2 2 0 012-2h2a2 2 0 012 2" />
                        </svg> -->
                    </div>
                    <div class="error-field">
                        This field can't be empty!
                    </div>
                </div>

                <div class="form-item" v-bind:class="{ error: formData.category.hasError }">
                    <label for="category">Category</label>
                    <select v-model="formData.category.value">
                        <option value='' disabled>Select category</option>
                        <option value="Development">Development</option>
                        <option value="Art">Art</option>
                    </select>
                    <div class="error-field">
                        This field can't be empty!
                    </div>
                </div>
            </form>
            <div class="nav-button-container">
                <button class="next-button review-button" @click.prevent="checkFormInputs">
                    Next
                    <svg xmlns="http://www.w3.org/2000/svg" fill="none" viewBox="0 0 24 24" stroke="currentColor">
                        <path stroke-linecap="round" stroke-linejoin="round" stroke-width="2" d="M17 8l4 4m0 0l-4 4m4-4H3" />
                    </svg>
                </button>
            </div>
        </div>

        <div v-show="activeTab==2" class="tab tab2">
            <div class="custom-card">
                <div class="custom-card-item">
                    <p class="card-item-title">Recipient wallet address</p>
                    {{this.formData.walletAddress.value}}
                </div>
                <div class="custom-card-item">
                    <p class="card-item-title">Rating</p>
                    {{this.formData.rating.value}}
                </div>
                <div class="custom-card-item">
                    <p class="card-item-title">Review</p>
                    {{this.formData.review.value}}
                </div>
                <div class="custom-card-item">
                    <p class="card-item-title">Reference link</p>
                    {{this.formData.referenceLink.value}}
                </div>
                <div class="custom-card-item">
                    <p class="card-item-title">Category</p>
                    {{this.formData.category.value}}
                </div>
            </div>
            <div class="nav-button-container">
                <button class="back-button review-button" @click.prevent="activeTab=1">
                    <svg xmlns="http://www.w3.org/2000/svg" fill="none" viewBox="0 0 24 24" stroke="currentColor">
                        <path stroke-linecap="round" stroke-linejoin="round" stroke-width="2" d="M7 16l-4-4m0 0l4-4m-4 4h18" />
                    </svg>
                    Back
                </button>
                <button class="next-button review-button" @click.prevent="activeTab=3, preview.isCompleted = true">
                    Next
                    <svg xmlns="http://www.w3.org/2000/svg" fill="none" viewBox="0 0 24 24" stroke="currentColor">
                        <path stroke-linecap="round" stroke-linejoin="round" stroke-width="2" d="M17 8l4 4m0 0l-4 4m4-4H3" />
                    </svg>
                </button>
            </div>
        </div>

        <div v-show="activeTab==3" class="tab tab3">
            <div class="nav-button-container">
                <button class="back-button review-button" @click.prevent="activeTab=2">Back</button>
            </div>
        </div>
  </div>
</template>

<script>
import StarRating from 'vue-star-rating';

export default {
    components: {
        StarRating
    },
    data() {
        return {
            activeTab: 1,
            formData: {
                isCompleted: false,
                walletAddress: {
                    value: '',
                    hasError: false
                },
                rating: {
                    value: '',
                    hasError: false
                },
                review: {
                    value: '',
                    hasError: false
                },
                referenceLink: {
                    value: '',
                    hasError: false
                },
                category: {
                    value: '',
                    hasError: false
                }
            },
            preview: {
                isCompleted: false
            }
            // walletAddress: '',
            // rating: '',
            // review: '',
            // referenceLink: '',
            // category: ''
        }
    },
    methods: {
        checkFormInputs() {
            let count = 0;

            if (this.formData.walletAddress.value != '') {
                this.formData.walletAddress.hasError = false;
                count++;
            } else {
                this.formData.walletAddress.hasError = true;
            }

            if (this.formData.rating.value != ''){
                this.formData.rating.hasError = false;
                count++;
            } else {
                this.formData.rating.hasError = true;
            }

            if (this.formData.review.value != '') {
                this.formData.review.hasError = false;
                count++;
            } else {
                this.formData.review.hasError = true;
            }

            if (this.formData.referenceLink.value != '') {
                this.formData.referenceLink.hasError = false;
                count++;
            } else {
                this.formData.referenceLink.hasError = true;
            }

            if (this.formData.category.value != ''){
                this.formData.category.hasError = false;
                count++;
            } else {
                this.formData.category.hasError = true;
            }

            if (count == 5){
                this.formData.isCompleted = true;
                this.activeTab = 2;
            } else {
                this.formData.isCompleted = false;
                this.preview.isCompleted = false;
            }
        }
    }
}
</script>

<style scoped>

.review {
    width: 100%;
    display: flex;
    flex-direction: column;
    align-items: center;
}

.tab {
    width: 100%;
    max-width: 700px;
}

.stepper {
    display: flex;
    flex-direction: row;
    margin-bottom: 50px;
}

.stepper-item {
    display: flex;
    flex-direction: row;
    align-items: center;
    justify-content: center;
    padding: 10px;
}

.stepper-icon {
    display: flex;
    flex-direction: row;
    align-items: center;
    justify-content: center;
    margin-right: 10px;
    width: 50px;
    height: 50px;
    background-color: #FBAB7E;
    background-image: linear-gradient(62deg, #FBAB7E 0%, #F7CE68 100%);
    border-radius: 50%;
}

.stepper-number {
    font-weight: bold;
    color : white;
    line-height: 1;
}

.stepper-icon.active {
    background: transparent;
}

.stepper-icon.active .stepper-number {
    display: none;
}

.stepper-icon svg {
    width: 50px;
    display: none;
    color: green;
}

.stepper-icon.active svg{
    display: block;
}

#review-form {
    display: grid;
    gap: 20px;
}

.form-item {
    display: flex;
    flex-direction: column;
}

.form-item.error input,
.form-item.error textarea,
.form-item.error select {
    border: solid 1px red;
    outline-color: red;
}

.form-item.error label {
    color : red;
}

.error-field {
    display: none;
    font-size: 14px;
    margin-top: 10px;
    color: red;
}

.form-item.error .error-field {
    display: block;
}

input, textarea, select {
    width: 100%;
    display: block;
    padding: 5px 10px;
}

label {
    margin-bottom: 10px;
}

.input-wrapper {
    position: relative;
}

.custom-card {
    display: grid;
    gap: 10px;
    padding: 30px;
    border-radius: 10px;
    background-color: white;
    box-shadow: rgba(100, 100, 111, 0.2) 0px 7px 29px 0px;
}

.card-item-title {
    font-weight: bold;
}

.nav-button-container {
    margin-top: 30px;
    display: flex;
    flex-direction: row;
    align-items: center;
    justify-content: space-between;
}

.review-button {
    display: flex;
    flex-direction: row;
    justify-content: center;
    align-items: center;
    padding: 10px 25px;
    color: grey;
    font-weight: bold;
    border: none;
    outline: none;
    cursor: pointer;
    border-radius: 10px;
    min-width: 150px;
}

.review-button svg {
    width: 25px;
}

.next-button {
    margin-left: auto;
}

.next-button svg {
    margin-left: 10px;
} 

.back-button svg {
    margin-right: 10px;
} 

</style>