<template>
  <div class="dashboard">
      <TwitterInput @clicked="twitterResponse" />
      <Review v-if="isTwitterConnected" style="margin-top: 50px;" />
  </div>
</template>

<script>
import Review from '@/components/Review.vue'
import TwitterInput from '@/components/TwitterInput.vue'

export default {
    name: 'Dashboard',
    components: {
        Review, TwitterInput
    },
    data() {
        return {
            isTwitterConnected: false
        }
    },
    methods: {
        twitterResponse(isConnected) {
            if (isConnected) {
                this.isTwitterConnected = true;
            }
            else {
                this.isTwitterConnected = false;
            }
        }
    }
}
</script>

<style scoped>
.dashboard {
    padding: 30px;
}
</style>