<template>
    <div class="twitter-review" v-bind:class="{ error: error }">
        <h2 class="review-title">Verify your identity</h2>
        <p class="review-text">Tired of being an anonymous wallet address? Link your address to your Twitter account.</p>
        <div class="twitter-review-container">
        <img class="twitter-img" src="../assets/img/placeholder.png" alt="Twitter image">
        <input class="twitter-handle-input" placeholder="Enter your Twitter @handle" type="text" v-model.trim="twitterHandle">
        <button v-if="!connected" id="twitter-connect-btn" class="twitter-btn" @click.prevent="connectTwitter">
            Connect
            <Loader v-show="isLoading" />
        </button>
        <button v-else id="twitter-disconnect-btn" class="twitter-btn" @click.prevent="disconnectTwitter">
            Disconnect
            <Loader v-if="isLoading" />
        </button>
        </div>
        <div class="error-msg">
            Please enter a valid twitter handle. It starts with @
        </div>
    </div>
</template>

<script>
import Loader from '@/components/Loader.vue'

export default {
    name: 'Twitter',
    components: {
        Loader
    },
    data() {
        return {
            twitterHandle: '',
            error: false,
            connected: false,
            isLoading: false
        }
    },
    methods: {
        connectTwitter(){
            this.startLoading('con')

            if (this.twitterHandle != '') {
                if (this.twitterHandle.charAt(0) != '@') {
                    this.error = true;
                }
                else {
                    document.querySelector('.twitter-handle-input').disabled = true;
                    this.error = false;
                    this.connected = true;
                    this.$emit('clicked', true);
                }
            } else {
                this.error = true;
            }

            this.stopLoading('con');
        },
        disconnectTwitter(){
            this.startLoading('dis')

            this.twitterHandle = '';
            document.querySelector('.twitter-handle-input').disabled = false;
                   
            this.connected = false;
            this.$emit('clicked', false);

            this.stopLoading('dis');
        },
        startLoading(type){
            if (type == 'con'){
                document.querySelector('#twitter-connect-btn').disabled = true;
            } else {
                document.querySelector('#twitter-disconnect-btn').disabled = true;
            }
            this.isLoading = true;
        }, 
        stopLoading(type){
            if (type == 'con'){
                document.querySelector('#twitter-connect-btn').disabled = false;
            } else {
                document.querySelector('#twitter-disconnect-btn').disabled = false;
            }
            this.isLoading = false;
        }
    }
}
</script>

<style>
.review-title {
    font-size: 25px;
}

.review-text {
    font-size: 16px;
}

.twitter-review-container {
    max-width: 800px;
    display: flex;
    flex-direction: row;
    flex-wrap: wrap;
    margin-top: 20px;
    padding: 20px;
    border-radius: 10px;
    /* background-color: #F7F7F7; */
    background-color: white;
    box-shadow: rgba(100, 100, 111, 0.2) 0px 7px 29px 0px;
}

.twitter-img {
    width: 50px;
    height: 50px;
    border-radius: 50%;
    object-fit: cover;
}

.twitter-handle-input {
    margin-left: 25px;
    border: 0;
    outline: none;
    background: none;
    font-size: 14px;
    color: #3A3A3A;
    min-width: 190px;
}

.twitter-btn {
    position: relative;
    margin-left: auto;
    padding: 10px 15px;
    /* color: #5C5C5C; */
    color: white;
    font-size: 16px;
    font-weight: bold;
    border: none;
    border-radius: 10px;
    cursor: pointer;
    overflow: hidden;
}

#twitter-connect-btn {
    color: white;
    background-color: #FBAB7E;
    background-image: linear-gradient(62deg, #FBAB7E 0%, #F7CE68 100%);
}

#twitter-disconnect-btn {
    background-color: grey;
}

.error-msg {
    display: none;
    margin-top: 10px;
    color: red;
}

.twitter-review.error .twitter-review-container{
    border: 1px solid red;
}

.twitter-review.error .error-msg{
    display: block;
}

@media only screen and (min-width: 768px) {
  
    .review-title {
        font-size: 30px;;
    }

    .review-text {
        font-size: 25px;
    }

    .dashboard {
        padding: 50px;
    }

    .twitter-img {
        width: 60px;
        height: 60px;
    }

    .twitter-handle-input {
        min-width: 350px;
        font-size: 18px;
    }

    #twitter-connect-btn {
        padding: 15px 25px;
        font-size: 18px;
    }
}
</style>