<template>
  <div id="top-bar">
      <div id="logo">metaKAFE</div>
      <button id="login-btn"
      @click="onClickConnect" >
            <Loader v-if="isLoading" style="color: white" />
            <img src="@/assets/img/metamask-logo.svg"
            />
            <p class="button-text">Connect your wallet</p>
      </button>
  </div>
</template>

<script>
import Loader from '@/components/Loader.vue';

export default {
    name: "Top bar",
    components: {
        Loader
    },
    data(){
        return {
            isLoading: false,
            address: ''
        }
    },
    mounted() {
        this.getAccount();
    },
    methods: {
        isMetaMaskInstalled() {
            console.log('here2')
            if (typeof window.ethereum !== 'undefined'
            || (typeof window.web3 !== 'undefined')) {
                console.log('MetaMask is active');
                return true;
            } else {
                console.log('MetaMask is not available');
                return false;
            }
        },
        async onClickConnect(){
            try {
                let isMetaMask = this.isMetaMaskInstalled();
                if (!isMetaMask) {
                    console.log('Please Install Metamask!');
                    return;
                }

                if (this.address != ''){
                    console.log('Address is not empty!');
                    return;
                }

                this.startLoading();
                //const forwarderOrigin = 'http://localhost:9010';
                //We create a new MetaMask onboarding object to use in our app
                //const onboarding = new MetaMaskOnboarding({ forwarderOrigin });

                //On this object we have startOnboarding which will start the onboarding process for our end user
                //onboarding.startOnboarding();

                // Will open the MetaMask UI
                // You should disable this button while the request is pending!
                await window.ethereum.request({ method: 'eth_requestAccounts' });

                this.getAccount();
            } catch (error) {
                console.error(error);
            }
        },
        async getAccount() {
            let isMetaMask = this.isMetaMaskInstalled();
            if (!isMetaMask) {
                console.log('Please Install Metamask!');
                return;
            }

            const accounts = await window.ethereum.request({ method: 'eth_accounts' });

            //We take the first address in the array of addresses and display it
            if (accounts && accounts.length > 0) {
                this.address = accounts[0];
                document.querySelector('.button-text').innerHTML = accounts[0]; 
            }

            this.endLoading();
            
        },
        startLoading(){
            document.querySelector('#login-btn').disable = true;
            this.isLoading = true;
        },
        endLoading(){
            document.querySelector('#login-btn').disable = false;
            this.isLoading = false;
        }
    }
    
}
</script>

<style scoped>
#logo {
    font-size: 25px;
    font-weight: bold;
    color: white;
}

#top-bar,
#login-btn {
    display: flex;
    flex-direction: row;
    align-items: center;
}

#top-bar {
    width: 100%;
    justify-content: space-between;
    flex-wrap: wrap;
    padding: 15px 25px;
    background-color: #FBAB7E;
    background-image: linear-gradient(62deg, #FBAB7E 0%, #F7CE68 100%);
}

#login-btn {
    position: relative;
    justify-content: center;
    padding: 10px;
    width: 220px;
    border: none;
    border-radius: 10px;
    cursor: pointer;
    background-color: white;
    overflow: hidden;
}

#login-btn img {
    width: 25px;
    margin-right: 10px;
}

.button-text {
     text-overflow: ellipsis;
     white-space: nowrap;
     overflow: hidden;
}
</style>